import Axios from "axios";
import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import Skeleton from "react-loading-skeleton";
import { connect } from "react-redux";
import { reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import { berhasil, gagal } from "../Alert";
import { checkReplikasiValidasi } from "../FindIndex";
import FormModalValidasiStok from "./Element/FormModalValidasiStok";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptoState = (state) => {
  return {
    username: state.provinsi.username,
  };
};
class ValidasiStokJualLM extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Nama Aktor",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            var hasil = checkReplikasiValidasi("FakturTerpilih", row.noFaktur);
            if (hasil === -1) {
              return (
                <div className="text-center">
                  <button
                    className="btn btn-warning mr-2"
                    data-toggle="modal"
                    data-target="#validasiModal"
                    onClick={(e) =>
                      this.setState({
                        noFaktur: row.noFaktur,
                        bruto: row.bruto,
                      })
                    }
                  >
                    <i className="fa fa-check-circle mr-1"></i>
                    Pilih
                  </button>
                </div>
              );
            }
          },
        },
      ],
      listValidasiBarang: [
        {
          tanggal: "2020-12-12",
          noFaktur: "NOFAKTUR-0001",
          netto: "100",
        },
      ],
    };
  }
  componentDidMount() {
    this.getData();
  }

  getData() {
    Axios.get(serverNonMaster + "cst/dc/lm/heads?statusValid=VALID").then(
      (res) =>
        this.setState({
          listValidasiBarang: res.data.filter(
            (list) => list.tipeTransaksi === "JUAL LM" && list.bruto > 0
          ),
        })
    );
  }
  handleModal(data) {
    var simpan = {
      noFaktur: data.noFaktur,
      barangDetails: [
        {
          bruto: data.jumlah,
        },
      ],
      footerDocument: {
        inputBy: this.props.username,
      },
    };
    console.log(JSON.stringify(simpan));
    Axios.put(serverNonMaster + "cst/dc/lmoutstand", simpan)
      .then(() => berhasil("Validasi Stok berhasil \nTerima kasih"))
      .then(() => this.getData())
      .catch(() =>
        gagal("Mohon maaf, Sepertinya koneksi internet sedang bermasalah")
      );
    this.props.dispatch(reset("FormModalValidasiStok"));
    document.getElementById("dismiss_modal1").click();
    this.props.dispatch(getUUID());
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <h1>Validasi Stok Penjualan LM</h1>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-12 mt-3">
                      {this.state.listValidasiBarang ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.state.listValidasiBarang}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <Skeleton
                          width={"100%"}
                          height={50}
                          count={5}
                          className="mb-1"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() =>
                            this.kirimvalidasi(this.state.tipeTransaksi)
                          }
                        >
                          <i className="fa fa-check mr-3"></i>
                          VALIDASI
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="validasiModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Validasi Stok Penjualan LM
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormModalValidasiStok
                  onSubmit={(data) => this.handleModal(data)}
                  noFaktur={this.state.noFaktur}
                  bruto={this.state.bruto}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(ValidasiStokJualLM);
